@import '../Styles/variableSass/variable';

.footer {
  width: 100%;
  height: 20vh;
  background-color: $textColor2;
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;

  .headerIcons {
    display: flex;
    flex-direction: row;
    justify-content: center;

    img {
      margin: 0 15px;
      width: 20px;
      cursor: pointer;

    }

  }

  .footerInfo {
    color: $textColor;
        font-weight:bold ;
  }
}
